h1{
  text-align: center;
  background-color: gold;
  padding: 15px;
  border-radius: 50px;
  margin: 0px 100px;
}

h1:hover{
  transform: scale3d(1.02,1.02,1.02);
  box-shadow: 0 0 30px black;
  transition: 0.75s;
}

h2{
  margin: 50px 200px 25px 200px;
  text-align: center;
  color: white;
  font-family: 'Times New Roman', Times, serif;
  background-color: rgb(36, 77, 227);
  padding: 10px;
  border: 3px solid black;
  border-radius: 15px;
}

input{
  display: block;
  margin: 0px 200px;
  background-color: aqua;
  height: 30px;
  border-radius: 10px;
  border: 2px solid ;
  text-align: center;
  font-weight: bold;
  font-size: x-large;
}
input:hover{
  background-color: lightgreen;
}

button{
  margin: 20px 50px 50px 50px;
  background-color: black;
  color: white;
  font-weight: bolder;
  font-size: x-large;
  border-radius: 15px;
  padding: 10px;

}

button:hover{
  border: 3px solid orange;
}

.btn{
  background-color: tomato;
}
.btn:hover{
  border: 3px solid green;
}
div{
  margin: 100px;
}

fieldset{
  margin: 10px 200px 0 200px;
  padding: 30px 30px 0 30px; 
  border-radius: 50px;
  border: 3px solid grey;
  display: inline;
}
legend{
  text-align: center;
}
